import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { PaginaEnemComponent } from './pagina-enem.component';

const routes: Routes = [
  { path: '', component: PaginaEnemComponent },
];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class PaginaEnemRoutingModule { }
